.samo-traka {
	position: relative;
	bottom: 0;
	height: 1.5rem;
	width: 100%;
}

.black-footer-content {
	color: white;
	position: fixed;
	bottom: 0;
	display: flex;
	align-items: center;
	height: 1.5rem;
	min-width: 20rem;
	animation: 20s slideTextInsideFooter linear infinite;
	white-space: nowrap;
}
.black-footer-content p {
	font-size: 0.9rem;
	margin-left: 3rem;
	position: relative;
	z-index: 20;
}

.black-footer {
	background-color: #333;
	position: relative;
	bottom: 0;
	margin-left: -200rem;
	width: 400rem;
	z-index: -5;
	height: 1.5rem;
}
@keyframes slideTextInsideFooter {
	0% {
		margin-left: -37rem;
	}
	100% {
		margin-left: 115vw;
	}
}
