.NavbarContact-container{
    justify-content: flex-end;
    margin-bottom: -1.5rem;
    color: rgba(0, 0, 0, 0.89);
}
.NavbarContact-container p{
    padding-left: 1rem;
}
.NavbarContact-container p i{
    color:rgba(4, 74, 155, 0.89);
    font-size: 1.4rem;
    cursor: pointer;
}

@media screen and (max-width: 576px){
    .NavbarContact-container{
        transform: scaleX(0.9);
        min-width: 100vw;
        margin-left: -3.8rem;
    }
    .NavbarContact-container p{
        padding-left: 0.5rem;
    }
}