header{
    
    background: linear-gradient(rgba(160, 179, 92, 0.87), transparent);
    padding: 0.5rem 5rem;

}

@media screen and (max-width: 750px){
    header nav li{
        padding: 0 0.3rem;
    }
    header{
        padding: 1rem 3rem;
    }
}
@media screen and (max-width: 625px){
    header{
        padding: 1rem 2rem;
    }
    header nav{
        min-width: 350px;
        margin-left: -30px;
       }
}

.hamurger-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 7rem;
    width: 10rem;
    position: absolute;
    margin-left: -10rem;
    background-color: rgb(131, 141, 102);
    border-radius: 5px;
    box-shadow: 0px 13px 31px 9px rgba(0,0,0,0.25);
    z-index: 5;
    
}
.hamurger-container i{
   transform: translateX(4.5rem);

}
.hamurger-links-container{
    
    background-color: rgb(161, 170, 129);
    width: 90%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    border-radius: 5px;
    box-shadow: inset 0px 0px 24px 8px rgba(0,0,0,0.35)
}
.hamurger-links-container a{
    padding-left: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}

.header-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    opacity: 0;
	animation: 1s slidefadeNavbarContent 0.4s forwards;
}
@keyframes slidefadeNavbarContent {
	100% {
		opacity: 1;
	}
}

header div, header div a{
    margin-top: -8px;
    color: white;
    text-decoration: none;
    
}
.header-logo{
    display: flex;
}
.header-logo li{
    color: white;
}
.header-logo h2.zama{
    transform: translateY(7px);
    font-size: 1.5rem;
}
.header-logo h2.began{
    transform: translate(-2px, 6px);
    font-family: 'Redressed', cursive;
}

header div a .cupping-icon-logo{
    transform: translate(-107px, 29px);
	background-image: url(./cupping-icon-logo.png);
	width: 50px;
	height: 25px;
	background-size: contain;
	background-repeat: no-repeat;
	opacity: 0.8;
}

header nav{
 margin-top: -1rem;
 color: white;
 list-style: none;
 display: flex;
 
}

header nav li{
    padding: 0 1rem;
}

.nav-linkovi{
    color: rgba(255, 255, 255, 0.651);
    text-decoration: none;
    font-size: 1.2rem;
}
.nav-linkovi:hover{
    color: rgb(255, 255, 255);
}

.nav-linkovi-active{
    color: rgb(255, 255, 255);
}
